import React, { useCallback, useEffect } from "react";
import { useHistory } from "react-router-dom";
import LeftPanel from "../components/LeftPanel";
import classes from "./Chat.module.css";
import pin from "../assets/pin.png";
import axios from "axios";
import { Avatar } from "@material-ui/core";
import Pusher from "pusher-js";
import { formatRelative, subDays } from "date-fns";

function Chat({ message, setMessage, messages, setMessages, user, setUser, clients, setClients }) {
    // const [message, setMessage] = useState("");
    // const [messages, setMessages] = useState([]);
    // const [clients, setClients] = useState([]);
    // const [user, setUser] = useState({});

    const history = useHistory();

    useEffect(() => {
        console.log(clients, "clients");
        console.log(messages, "messages");
        // setUser({});
    }, []);

    // console.log(changes, "Change here");
    // changes.forEach((guy) => {
    //     console.log(guy);
    // });

    // useEffect(() => {
    //     const pusher = new Pusher(`df62896bae0aebeae741`, {
    //         cluster: "ap2",
    //     });

    //     // local storage stuff

    //     let clients = JSON.parse(localStorage.getItem("clients")) || [];
    //     console.log(clients, "clients");
    //     setClients(clients);

    //     const date = formatRelative(subDays(new Date(), ""), new Date());
    //     const channel = pusher.subscribe("messages");
    //     channel.bind("inserted", (newMessage) => {
    //         clients = clients.filter((client) => client.userId !== newMessage.userId || !newMessage.consumer);
    //         if (newMessage.userId) {
    //             newMessage.consumer && clients.unshift({ ...newMessage, date });
    //         }
    //         localStorage.setItem("clients", JSON.stringify(clients));

    //         let currentTalk = JSON.parse(localStorage.getItem(newMessage.userId)) || [];
    //         currentTalk = [...currentTalk, { ...newMessage, date }];
    //         localStorage.setItem(newMessage.userId, JSON.stringify(currentTalk));

    //         if (newMessage.userId === user.userId) {
    //             console.log(user);
    //             setMessages([...messages, { ...newMessage, date }]);
    //         } else {
    //             setMessages([...messages, newMessage]);
    //         }
    //     });
    //     return () => {
    //         channel.unbind_all();
    //         channel.unsubscribe();
    //     };
    // }, [messages]);
    console.log(messages);

    const setRef = useCallback((node) => {
        if (node) {
            node.scrollIntoView({ scroll: true });
        }
    }, []);

    const name1 = "Admin";
    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!message) return;
        try {
            await axios.post(`https://plein-mobile.herokuapp.com/chat`, {
                name: name1,
                message,
                consumer: false,
                userId: user.userId || null,
            });
            setMessage("");
        } catch ({ response }) {
            console.log(response.data.message);
        }
    };

    const truncateMsg = (msg, n) => {
        return msg?.length > n ? msg.substr(0, n - 1) + "..." : msg;
    };

    const handleCurrent = (user) => {
        setUser(user);
        const chat = JSON.parse(localStorage.getItem(user.userId));
        setMessages(chat);
        console.log(chat);
        setTimeout(() => {
            axios.delete("https://plein-mobile.herokuapp.com/chat/remove");
            localStorage.clear();
            history.go("/");
        }, [21600000]);
    };

    return (
        <div className="d-flex">
            <LeftPanel />
            <div className={`${classes.main} "d-flex  flex-column"`}>
                <h2 className={classes.title}>Chat</h2>
                <div className={classes.chatPanel}>
                    <div className={classes.chatList}>
                        <h2>Messages</h2>
                        <div className={classes.users}>
                            {clients.map(({ name, userId, message, date }) => (
                                <div onClick={() => handleCurrent({ name, userId, message, date })} key={userId} className={classes.user}>
                                    <Avatar
                                        style={{ background: "#444", fontSize: "x-large" }}
                                        className={classes.Avatar}
                                        alt={name}
                                        src={name}
                                    />
                                    <div className={classes.titleContent}>
                                        <div className={classes.titleHead}>
                                            <h3>{name}</h3>
                                            <p>{date.slice(8)}</p>
                                            {/* <p>{`${hours}:${currentMinutes} ${ampm}`}</p> */}
                                        </div>
                                        <div className={classes.titleHead}>
                                            <span>{truncateMsg(message, 20)}</span>
                                            {/* <p className={classes.titleUnRead}> {messages.length}</p> */}
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className={classes.chatBox}>
                        <h3 style={{ marginBottom: "auto" }} className={classes.currentUser}>
                            {user.name || "Notifications"}
                        </h3>
                        <div className={classes.messages}>
                            {messages?.map(({ message, name }, index) => {
                                const lasteMessage = messages.length - 1 === index;
                                const consumer = name === name1;
                                return (
                                    <div
                                        ref={lasteMessage ? setRef : null}
                                        key={index}
                                        className={!consumer ? classes.messenger : classes.receiver}
                                    >
                                        <Avatar
                                            style={{ background: "#444", fontSize: "x-large" }}
                                            className={classes.Avatar}
                                            alt={name}
                                            src={name}
                                        />
                                        <p>{message}</p>
                                    </div>
                                );
                            })}
                        </div>
                        <form className={classes.chatInput}>
                            <textarea
                                disabled={!user.userId}
                                value={message}
                                onChange={(e) => setMessage(e.target.value)}
                                type={"text"}
                                placeholder={"Type your text"}
                            />
                            <div className={classes.chatInputOptions}>
                                <button onClick={handleSubmit} className={classes.chatButton} type={"submit"}>
                                    Send
                                </button>
                                <div type={"button"} className={classes.chatInputOption}>
                                    <img src={pin} alt={""} />
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Chat;
